.choose {
    display: flex;
    flex-direction: column;
    width: 120px;
    font-size: 18px;
    // line-height: 18px;
    position: fixed;

    p:not(:first-child) {
        border-bottom: 1px solid rgb(73, 73, 73);
        color: #fff;
    }

    p:first-child {
        font-size: 20px;
        color: #fff;
    }

    input {
        margin-top: 20px;
        margin-right: 10px;
        width: 20px;
        height: 20px;
        cursor: pointer;
    }

    label {
        cursor: pointer;

    }

    button {
        margin-top: 20px;
        background: #ac4343;
        color: #fff;
        margin-top: 30px;
        margin-right: 10px;
        padding: 10px 20px;
        border: none;
        cursor: pointer;
        outline: none;


        &:hover {
            background: #f6f6f6;
            color: #000;
            font-weight: bold;
            // font-size: 18px;
        }

    }

    .sortMin,
    .sortMax {
        font-size: 14px;
        background-color: transparent;
        padding: 5px;
        width: 120px;

    }
}